<template>
	<view class='employ'>
		
		<u-form
				labelPosition="top"
				:model="dataValue"
				:rules="rules"
				ref="form1"
		>	
		
		<view class='header'>
			<view class='exit'><u-icon name='close'></u-icon></view>
			<view class='text'>发布需求</view>
			<view class='button'><u-button @click="onSubmit" type='warning' text='发布' round ></u-button></view>
		</view>
		
		<view class='content'>
			<view 
		      v-for="(item, index) in views" 
		      :key="index" 
		      :class="['box', { 'active': activeIndex === index }]" 
		      @click="setActive(index)">
		      {{ item }}
		    </view>
			
			
			
			
		</view>
		
		<view class='detail'>
			<view class='cell'>
				<u-form-item
									label="伙伴"
									prop="hb"
									
									ref="item1"
							>
				<u-textarea v-model='dataValue.hb' placeholder="请输入问题" border="bottom" height=40 maxlength="40" count autoHeight></u-textarea>
			
				</u-form-item>
			</view>
			
			<view class='cell'>
				<text>你的名义</text>
				<u-textarea v-model='dataValue.head' placeholder="请输入问题" border="none" height=40 maxlength="40" count autoHeight></u-textarea>
				
			</view>
			<view class='cell'>
				<text>详细描述</text>
				<u-textarea v-model='dataValue.detail' placeholder="请输入问题" border="none" height=40 maxlength="40" count autoHeight></u-textarea>
				
			</view>
			
			<view class='cell'>
				<text>往期风采</text>
					<!-- <u-upload></u-upload> -->
					<uni-file-picker
					v-model='imageValue'
					file-mediatype="image"
					mode="grid"
					@success="uploadSuccess"
					:imageStyles="imgStyle"
					:autoUpload="false"
					ref='files'
					>
					</uni-file-picker>
			</view>
		</view>
		
		</u-form>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				imgStyle:{
					height:80,
					width:80
				},
				imageValue:[],
				picurls:[],
				rules:{},
				dataValue:{
					hb:'',
					head:'',
					detail:'',
					type:'',
					
				},
				username:'',
				list:['社团','社团','科研','兼职'],
				curNow:0,
				views: ['社团', '科研', '搭伙', '兼职'] ,// 视图内容数组
				activeIndex: null, // 当前激活的view的索引

			};
		},
		methods:{
			uploadSuccess(e){
			console.log(e)	
			this.picurls = e.tempFilePaths
			},
			async onSubmit(e){
			 try{
				await this.$refs.files.upload()

				const that =this
				let type = this.list[that.curNow]
				
				this.dataValue.type=type
				console.log(this.dataValue)
				let detail = this.dataValue
				uniCloud.callFunction({
					name:'art_add_row',
					data:{
						detail,
						picurls:this.picurls,
						hType:'dz'
						
					}
				   }).then(res=>{
					console.log('res',res)
					  uni.showToast({
					          title: '提交成功',
					          icon: 'success'
					        });
							
					setTimeout(()=> { uni.reLaunch({
						url:'/pages/add/add'
					})},1000)
					
							
				})
			}catch(error){
				console.error('上传失败',error)
			}}
			,
			sectionChange(e){
				this.curNow =e
			},
			setActive(index) {
			      this.activeIndex = index; // 设置当前激活的view的索引
					console.log(index)
			    }
		}
	}
</script>

